//   
export const comma = function (str) {
  return String(str).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

//   
export const filterComma = function (str) {
  return String(str).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}
//  
export const filterDate = function (str) {
  let date = str.split(' ')[0].split('/')
  return date[2] + " " + date[0] + " " + date[1] + ""
}